.stars {
  display: flex;
  gap: 8px;
  padding-left: 2px;

  &__star {
    width: 12px;
    height: 12px;
    background-image: url('./../images/star.svg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &:nth-child(-n+4) {
      background-image: url('./../images/star-active.svg');
    }
  }
}
