* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 0;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 18px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.card__cost {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__amount {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  gap: 8px;
  padding-left: 2px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 12px;
  height: 12px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

